// src/components/AlchanLayout.js
//  UI    - Tailwind CSS 
//   : /  lazy loading 

import React, { useState, useEffect, useCallback, lazy, Suspense } from "react";
import { Routes, Route, Navigate, useLocation } from "react-router-dom";
import { useAuth } from "../contexts/AuthContext";
import { ItemProvider } from "../contexts/ItemContext"; //  []   
import AlchanSidebar from "./AlchanSidebar";
import AlchanHeader from "./AlchanHeader";
import MobileNav from "./MobileNav";
import PWAInstallPrompt from "./PWAInstallPrompt";
import UpdateNotification from "./UpdateNotification";
import { useServiceWorker } from "../hooks/useServiceWorker";
import { AlchanLoadingScreen } from "./ui/Skeleton";
import { WifiOff } from "lucide-react";
import { getStreakInfo } from "./DailyReward";
import EconomicEventBanner from "./EconomicEventBanner";
const DailyRewardBanner = lazy(() =>
  import("./DailyReward").then((m) => ({ default: m.DailyRewardBanner })),
);
const WelcomePopup = lazy(() => import("./WelcomePopup"));
const HelpButton = lazy(() => import("./HelpButton"));
import { db, doc, updateDoc, increment } from "../firebase";
import globalCacheService from "../services/globalCacheService";
import { logger } from "../utils/logger";

//    - Dashboard lazy 
const Dashboard = lazy(() => import("../pages/dashboard/Dashboard"));

//  []       -  
const ItemStore = lazy(() => import("../pages/market/ItemStore"));
const MyItems = lazy(() => import("../pages/my-items/MyItems"));

//  []       -  
const PersonalShop = lazy(() => import("../pages/market/PersonalShop"));
const Banking = lazy(() => import("../pages/banking/Banking"));
const MyProfile = lazy(() => import("../pages/my-profile/MyProfile"));
const MyAssets = lazy(() => import("../pages/my-assets/MyAssets"));

//    -   (  )
const OmokGame = lazy(() => import("../pages/games/OmokGame"));
const ChessGame = lazy(() => import("../pages/games/ChessGame"));
const TypingPracticeGame = lazy(
  () => import("../pages/games/TypingPracticeGame"),
);

//  /  -  
const AdminApprovalPanel = lazy(
  () => import("../pages/admin/AdminApprovalPanel"),
);
const AdminItemPage = lazy(() => import("../pages/admin/AdminItemPage"));
const AdminDatabase = lazy(() => import("../pages/admin/AdminDatabase"));
const AdminEconomicEvents = lazy(
  () => import("../pages/admin/AdminEconomicEvents"),
);
const FirestoreDoctor = lazy(() => import("../pages/admin/FirestoreDoctor"));
const RecoverDonations = lazy(() => import("../pages/admin/RecoverDonations"));
const StudentManager = lazy(() => import("./StudentManager"));

//   (SuperAdmin)  
const SuperAdminDashboard = lazy(
  () => import("../pages/superadmin/SuperAdminDashboard"),
);

//      -  
const LearningBoard = lazy(() => import("../pages/learning/LearningBoard"));
const MusicRequest = lazy(() => import("../pages/music/MusicRequest"));
const MusicRoom = lazy(() => import("../pages/music/MusicRoom"));
const StudentRequest = lazy(() => import("../pages/student/StudentRequest"));
const StockExchange = lazy(() => import("../pages/banking/StockExchange"));
const RealEstateRegistry = lazy(
  () => import("../pages/real-estate/RealEstateRegistry"),
);
const NationalAssembly = lazy(
  () => import("../pages/government/NationalAssembly"),
);
const Government = lazy(() => import("../pages/government/Government"));
const Court = lazy(() => import("../pages/government/Court"));
const PoliceStation = lazy(() => import("../pages/government/PoliceStation"));
const Auction = lazy(() => import("../pages/market/Auction"));
const MoneyTransfer = lazy(() => import("../pages/banking/MoneyTransfer"));
const CouponTransfer = lazy(() => import("../pages/banking/CouponTransfer"));
const CouponGoalPage = lazy(() => import("../pages/coupon/CouponGoalPage"));

//      (  )
const FULLSCREEN_PAGES = [
  "/learning-games/omok",
  "/learning-games/science",
  "/learning-games/typing",
  "/court",
  "/national-assembly",
  "/music-room",
];

//    -      
const AlchanLoading = ({ message = " ..." }) => {
  return <AlchanLoadingScreen message={message} />;
};

// Protected Route 
const ProtectedRoute = ({ children }) => {
  const { user, loading } = useAuth();
  const location = useLocation();

  if (loading) {
    return <AlchanLoading />;
  }

  if (!user) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  return children;
};

// Admin Route 
const AdminRoute = ({ children }) => {
  const { user, userDoc, loading } = useAuth();
  const location = useLocation();

  if (loading) {
    return <AlchanLoading />;
  }

  if (!user) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  const isAdmin =
    userDoc?.isAdmin || userDoc?.role === "admin" || userDoc?.isSuperAdmin;
  if (!isAdmin) {
    return <Navigate to="/dashboard/tasks" replace />;
  }

  return children;
};

// / Route 
const TeacherRoute = ({ children }) => {
  const { user, userDoc, loading } = useAuth();
  const location = useLocation();

  if (loading) {
    return <AlchanLoading />;
  }

  if (!user) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  const isTeacher =
    userDoc?.isTeacher || userDoc?.isAdmin || userDoc?.isSuperAdmin;
  if (!isTeacher) {
    return <Navigate to="/dashboard/tasks" replace />;
  }

  return children;
};

//   (SuperAdmin)  Route 
const SuperAdminRoute = ({ children }) => {
  const { user, userDoc, loading } = useAuth();
  const location = useLocation();

  if (loading) {
    return <AlchanLoading />;
  }

  if (!user) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  if (!userDoc?.isSuperAdmin) {
    return <Navigate to="/dashboard/tasks" replace />;
  }

  return children;
};

//   
export default function AlchanLayout() {
  const location = useLocation();
  const { user, userDoc, loading, logout } = useAuth();

  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [isSidebarCollapsed, setIsSidebarCollapsed] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);
  const [showUpdateDismissed, setShowUpdateDismissed] = useState(() => {
    try {
      const dismissed = localStorage.getItem("alchan_update_dismissed");
      if (!dismissed) return false;
      // 24    
      return Date.now() - parseInt(dismissed) < 24 * 60 * 60 * 1000;
    } catch {
      return false;
    }
  });
  const [showDailyRewardPopup, setShowDailyRewardPopup] = useState(false);

  // PWA   
  const { updateAvailable, updateServiceWorker, isOnline } = useServiceWorker();

  //     -  ( )   
  useEffect(() => {
    if (
      userDoc?.uid &&
      !userDoc?.isAdmin &&
      !userDoc?.isSuperAdmin &&
      !userDoc?.isTeacher
    ) {
      const streakInfo = getStreakInfo(userDoc.uid);
      if (streakInfo.canClaim) {
        const timer = setTimeout(() => {
          setShowDailyRewardPopup(true);
        }, 500);
        return () => clearTimeout(timer);
      }
    }
  }, [
    userDoc?.uid,
    userDoc?.isAdmin,
    userDoc?.isSuperAdmin,
    userDoc?.isTeacher,
  ]);

  //     
  const handleDailyRewardClaim = useCallback(
    async (rewardAmount) => {
      if (!userDoc?.uid || !rewardAmount) return;
      try {
        const userRef = doc(db, "users", userDoc.uid);
        await updateDoc(userRef, { cash: increment(rewardAmount) });
        globalCacheService.invalidate(`user_${userDoc.uid}`);
        setTimeout(() => setShowDailyRewardPopup(false), 3000);
      } catch (error) {
        logger.error("   :", error);
      }
    },
    [userDoc?.uid],
  );

  // Jua  index.html preload  ( )

  //      
  useEffect(() => {
    const isFullscreenPage = FULLSCREEN_PAGES.some((page) =>
      location.pathname.startsWith(page),
    );
    if (isFullscreenPage && !isMobile) {
      setIsSidebarCollapsed(true);
    }
  }, [location.pathname, isMobile]);

  //    ( )
  useEffect(() => {
    let timeoutId;
    const handleResize = () => {
      clearTimeout(timeoutId);
      timeoutId = setTimeout(() => {
        const mobile = window.innerWidth < 768;
        setIsMobile(mobile);
        if (mobile) {
          setIsSidebarOpen(false);
        }
      }, 150);
    };

    window.addEventListener("resize", handleResize);
    handleResize();

    return () => {
      window.removeEventListener("resize", handleResize);
      clearTimeout(timeoutId);
    };
  }, []);

  const toggleSidebar = useCallback(() => {
    setIsSidebarOpen((prev) => !prev);
  }, []);

  const closeSidebar = useCallback(() => {
    setIsSidebarOpen(false);
  }, []);

  const toggleSidebarCollapse = useCallback(() => {
    setIsSidebarCollapsed((prev) => !prev);
  }, []);

  //        ( )
  if (!loading && !user) {
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  //   (loading ,  userDoc  )
  if (loading || (user && !userDoc)) {
    return <AlchanLoading />;
  }

  //   (  )
  if (location.pathname === "/login") {
    return <Navigate to={user ? "/dashboard/tasks" : "/login"} replace />;
  }

  //   
  if (!userDoc.classCode) {
    return (
      <div className="min-h-screen bg-slate-50 flex items-center justify-center p-4">
        <div className="bg-white rounded-3xl shadow-xl p-8 max-w-sm w-full text-center">
          <div className="w-16 h-16 bg-amber-100 rounded-2xl flex items-center justify-center mx-auto mb-4 text-3xl">
            
          </div>
          <h2 className="text-xl font-bold text-gray-900 mb-2">
              
          </h2>
          <p className="text-gray-500 mb-6">
                .
          </p>
          <button
            onClick={() => logout()}
            className="px-6 py-3 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-xl font-semibold transition-colors"
          >
            
          </button>
        </div>
      </div>
    );
  }

  return (
    //  [] ItemProvider   -     Firestore  
    <ItemProvider>
      <div className="min-h-screen bg-[#0a0a12] text-gray-100 font-sans selection:bg-indigo-500/30 selection:text-indigo-200 flex">
        {/* PC  */}
        <AlchanSidebar
          isOpen={isSidebarOpen}
          onClose={closeSidebar}
          isCollapsed={isSidebarCollapsed}
        />

        {/*    -    */}
        <main className="flex-1 min-w-0 md:min-h-screen relative bg-[#0a0a12] overflow-visible">
          {/*  */}
          <AlchanHeader
            toggleSidebar={toggleSidebar}
            isMobile={isMobile}
            isSidebarCollapsed={isSidebarCollapsed}
            onToggleSidebarCollapse={toggleSidebarCollapse}
          />

          {/*    */}
          <EconomicEventBanner />

          {/*   -  Suspense lazy loading  */}
          <div className="w-full pb-20 md:pb-4">
            <Suspense fallback={<AlchanLoading message="  ..." />}>
              <Routes>
                {/*   */}
                <Route
                  path="/dashboard/tasks"
                  element={
                    <ProtectedRoute>
                      <Dashboard />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/my-assets"
                  element={
                    <ProtectedRoute>
                      <MyAssets />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/my-profile"
                  element={
                    <ProtectedRoute>
                      <MyProfile />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/coupon-goal"
                  element={
                    <ProtectedRoute>
                      <CouponGoalPage />
                    </ProtectedRoute>
                  }
                />

                {/*  */}
                <Route
                  path="/learning-games/omok"
                  element={
                    <ProtectedRoute>
                      <OmokGame />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/learning-games/typing"
                  element={
                    <ProtectedRoute>
                      <TypingPracticeGame />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/learning-games/science"
                  element={
                    <ProtectedRoute>
                      <ChessGame />
                    </ProtectedRoute>
                  }
                />

                {/*  */}
                <Route
                  path="/item-shop"
                  element={
                    <ProtectedRoute>
                      <ItemStore />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/my-items"
                  element={
                    <ProtectedRoute>
                      <MyItems />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/item-market"
                  element={
                    <ProtectedRoute>
                      <PersonalShop />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/personal-shop"
                  element={
                    <ProtectedRoute>
                      <PersonalShop />
                    </ProtectedRoute>
                  }
                />

                {/*  */}
                <Route
                  path="/banking"
                  element={
                    <ProtectedRoute>
                      <Banking />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/stock-trading"
                  element={
                    <ProtectedRoute>
                      <StockExchange />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/auction"
                  element={
                    <ProtectedRoute>
                      <Auction />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/real-estate"
                  element={
                    <ProtectedRoute>
                      <RealEstateRegistry />
                    </ProtectedRoute>
                  }
                />

                {/*  */}
                <Route
                  path="/government"
                  element={
                    <ProtectedRoute>
                      <Government />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/national-assembly"
                  element={
                    <ProtectedRoute>
                      <NationalAssembly />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/court"
                  element={
                    <ProtectedRoute>
                      <Court />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/police"
                  element={
                    <ProtectedRoute>
                      <PoliceStation />
                    </ProtectedRoute>
                  }
                />

                {/*  */}
                <Route
                  path="/learning-board"
                  element={
                    <ProtectedRoute>
                      <LearningBoard />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/learning-board/music-request"
                  element={
                    <ProtectedRoute>
                      <MusicRequest user={user} />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/music-room/:roomId"
                  element={
                    <ProtectedRoute>
                      <MusicRoom user={user} />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/student-request/:roomId"
                  element={<StudentRequest />}
                />

                {/*   */}
                <Route
                  path="/admin/app-settings"
                  element={
                    <AdminRoute>
                      <Dashboard adminTabMode="generalSettings" />
                    </AdminRoute>
                  }
                />
                <Route
                  path="/admin/job-settings"
                  element={
                    <AdminRoute>
                      <Dashboard adminTabMode="jobSettings" />
                    </AdminRoute>
                  }
                />
                <Route
                  path="/admin/class-members"
                  element={
                    <AdminRoute>
                      <Dashboard adminTabMode="memberManagement" />
                    </AdminRoute>
                  }
                />
                <Route
                  path="/admin/coupon-transfer"
                  element={
                    <AdminRoute>
                      <CouponTransfer />
                    </AdminRoute>
                  }
                />
                <Route
                  path="/admin/money-transfer"
                  element={
                    <AdminRoute>
                      <MoneyTransfer />
                    </AdminRoute>
                  }
                />
                <Route
                  path="/admin/approvals"
                  element={
                    <AdminRoute>
                      <AdminApprovalPanel />
                    </AdminRoute>
                  }
                />
                <Route
                  path="/admin/activity-log"
                  element={
                    <AdminRoute>
                      <AdminDatabase />
                    </AdminRoute>
                  }
                />
                <Route
                  path="/admin/items"
                  element={
                    <AdminRoute>
                      <AdminItemPage />
                    </AdminRoute>
                  }
                />
                <Route
                  path="/admin/economic-events"
                  element={
                    <AdminRoute>
                      <AdminEconomicEvents />
                    </AdminRoute>
                  }
                />
                <Route
                  path="/admin/students"
                  element={
                    <TeacherRoute>
                      <StudentManager />
                    </TeacherRoute>
                  }
                />

                {/*   (SuperAdmin)  */}
                <Route
                  path="/super-admin"
                  element={
                    <SuperAdminRoute>
                      <SuperAdminDashboard />
                    </SuperAdminRoute>
                  }
                />
                <Route
                  path="/super-admin/*"
                  element={
                    <SuperAdminRoute>
                      <SuperAdminDashboard />
                    </SuperAdminRoute>
                  }
                />

                {/*  */}
                <Route
                  path="/doctor"
                  element={
                    <ProtectedRoute>
                      <FirestoreDoctor />
                    </ProtectedRoute>
                  }
                />
                <Route
                  path="/recover-donations"
                  element={
                    <ProtectedRoute>
                      <RecoverDonations />
                    </ProtectedRoute>
                  }
                />

                {/*   */}
                <Route
                  path="/"
                  element={<Navigate to="/dashboard/tasks" replace />}
                />
                <Route
                  path="*"
                  element={<Navigate to="/dashboard/tasks" replace />}
                />
              </Routes>
            </Suspense>
          </div>

          {/*  - PC */}
          <footer className="hidden md:block py-8 text-center text-sm text-gray-400 font-medium">
             2025  Corp. All rights reserved.
          </footer>
        </main>

        {/*    */}
        <MobileNav />

        {/* PWA   */}
        <PWAInstallPrompt />

        {/*   */}
        {updateAvailable && !showUpdateDismissed && (
          <UpdateNotification
            onUpdate={updateServiceWorker}
            onDismiss={() => {
              setShowUpdateDismissed(true);
              try {
                localStorage.setItem(
                  "alchan_update_dismissed",
                  Date.now().toString(),
                );
              } catch {}
            }}
          />
        )}

        {/*   */}
        {!isOnline && (
          <div className="fixed top-0 left-0 right-0 bg-amber-500 text-white text-center py-2 text-sm font-medium z-50 flex items-center justify-center gap-2">
            <WifiOff size={16} />
             .     .
          </div>
        )}

        {/*    */}
        <HelpButton />

        {/*     */}
        <WelcomePopup />

        {/*      */}
        {showDailyRewardPopup && userDoc?.uid && (
          <div
            className="fixed inset-0 flex items-center justify-center p-5 bg-black/70 backdrop-blur-sm z-[9999] animate-fadeIn"
            onClick={() => setShowDailyRewardPopup(false)}
          >
            <div
              className="w-full max-w-[400px] animate-slideUp"
              onClick={(e) => e.stopPropagation()}
            >
              <div className="text-right mb-2">
                <button
                  onClick={() => setShowDailyRewardPopup(false)}
                  className="w-8 h-8 rounded-full border-none cursor-pointer text-lg text-white flex items-center justify-center ml-auto bg-white/20"
                >
                  
                </button>
              </div>
              <DailyRewardBanner
                userId={userDoc.uid}
                onClaim={handleDailyRewardClaim}
              />
              <div className="text-center mt-3 text-[13px] text-white/60">
                  
              </div>
            </div>
          </div>
        )}

        {/*   index.css  */}
      </div>
    </ItemProvider>
  );
}

// AlchanLoading  export
export { AlchanLoading };
