// src/utils/numberFormatter.js

/**
 *    (, , , ,  ) .
 * @param {number|string} number -     
 * @param {string} unit -      (: '', '')
 * @returns {string}    
 */
export const formatKoreanNumber = (number, unit = "") => {
  // null, undefined 
  if (number === null || number === undefined || number === "") {
    return "0" + unit;
  }

  //    
  let num = number;
  if (typeof num === "string") {
    //    
    num = num.replace(/[,]/g, "").trim();
    num = parseFloat(num);
    if (isNaN(num)) {
      return "0" + unit;
    }
  }

  // 0 
  if (num === 0) {
    return "0" + unit;
  }

  //  
  const isNegative = num < 0;
  const absNumber = Math.abs(num);

  const units = [
    { value: 1e20, name: "" },
    { value: 1e16, name: "" },
    { value: 1e12, name: "" },
    { value: 1e8, name: "" },
    { value: 1e4, name: "" },
  ];

  const resultParts = [];
  let remaining = absNumber;

  //   
  for (const { value, name } of units) {
    if (remaining >= value) {
      const quotient = Math.floor(remaining / value);
      resultParts.push(`${quotient}${name}`); // (,)  toLocaleString 
      remaining %= value;
    }
  }

  //    
  if (remaining > 0) {
    //  
    if (remaining % 1 !== 0) {
      resultParts.push(remaining.toLocaleString('ko-KR', { maximumFractionDigits: 2 }));
    } else {
      resultParts.push(Math.floor(remaining)); // (,)  toLocaleString 
    }
  }

  //  
  let result = resultParts.join(" ");
  
  //  
  if (unit) {
    result += unit;
  }

  //   
  if (isNegative) {
    result = "-" + result;
  }

  return result;
};

/**
 *     
 * @param {number|string} amount -  
 * @returns {string}   
 */
export const formatKoreanCurrency = (amount) => {
  return formatKoreanNumber(amount, "");
};

/**
 *    
 * @param {number|string} quantity -  
 * @returns {string}   
 */
export const formatKoreanQuantity = (quantity) => {
  return formatKoreanNumber(quantity, "");
};

/**
 *   
 * @param {number|string} count -  
 * @returns {string}   
 */
export const formatCouponCount = (count) => {
  return formatKoreanNumber(count, "");
};

