// src/components/SettingsPanel.js
//    ( ,   )

import React, { useState } from 'react';
import { useTheme, FONT_SIZES } from '../contexts/ThemeContext';
import { useTutorial } from './TutorialGuide';
import {
  X, Moon, Sun, Type, RefreshCw, Bell, Volume2, Smartphone,
  ChevronRight, Check, HelpCircle
} from 'lucide-react';

export function SettingsPanel({ isOpen, onClose }) {
  const { isDarkMode, toggleDarkMode, fontSize, setFontSize } = useTheme();
  const { resetTutorial } = useTutorial();
  const [notifications, setNotifications] = useState(
    localStorage.getItem('alchan-notifications') !== 'false'
  );
  const [sounds, setSounds] = useState(
    localStorage.getItem('alchan-sounds') !== 'false'
  );

  const handleNotificationToggle = () => {
    const newValue = !notifications;
    setNotifications(newValue);
    localStorage.setItem('alchan-notifications', String(newValue));
  };

  const handleSoundToggle = () => {
    const newValue = !sounds;
    setSounds(newValue);
    localStorage.setItem('alchan-sounds', String(newValue));
  };

  const handleResetTutorial = () => {
    if (window.confirm('  ?')) {
      resetTutorial();
      onClose();
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-center justify-center p-4">
      <div className="bg-white dark:bg-gray-800 rounded-3xl w-full max-w-md max-h-[90vh] overflow-hidden shadow-2xl">
        {/*  */}
        <div className="flex items-center justify-between p-4 border-b border-gray-100 dark:border-gray-700">
          <h2 className="text-lg font-bold text-gray-900 dark:text-white"></h2>
          <button
            onClick={onClose}
            className="p-2 rounded-full hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
          >
            <X size={20} className="text-gray-500" />
          </button>
        </div>

        {/*   */}
        <div className="overflow-y-auto p-4 space-y-6">
          {/*   */}
          <section>
            <h3 className="text-sm font-semibold text-gray-500 dark:text-gray-400 mb-3 uppercase tracking-wider">
              
            </h3>

            {/*   */}
            <div className="bg-gray-50 dark:bg-gray-700/50 rounded-2xl p-4 space-y-4">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                  {isDarkMode ? (
                    <Moon size={20} className="text-indigo-500" />
                  ) : (
                    <Sun size={20} className="text-amber-500" />
                  )}
                  <div>
                    <p className="font-medium text-gray-900 dark:text-white"> </p>
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                          
                    </p>
                  </div>
                </div>
                <button
                  onClick={toggleDarkMode}
                  className={`w-12 h-7 rounded-full transition-colors relative ${
                    isDarkMode ? 'bg-indigo-500' : 'bg-gray-300'
                  }`}
                >
                  <div
                    className={`absolute top-1 w-5 h-5 bg-white rounded-full shadow transition-transform ${
                      isDarkMode ? 'left-6' : 'left-1'
                    }`}
                  />
                </button>
              </div>

              {/*   */}
              <div className="pt-4 border-t border-gray-200 dark:border-gray-600">
                <div className="flex items-center gap-3 mb-3">
                  <Type size={20} className="text-blue-500" />
                  <div>
                    <p className="font-medium text-gray-900 dark:text-white"> </p>
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                         
                    </p>
                  </div>
                </div>
                <div className="grid grid-cols-4 gap-2">
                  {Object.entries(FONT_SIZES).map(([key, { label }]) => (
                    <button
                      key={key}
                      onClick={() => setFontSize(key)}
                      className={`py-2 rounded-xl text-sm font-medium transition-all ${
                        fontSize === key
                          ? 'bg-indigo-500 text-white'
                          : 'bg-white dark:bg-gray-600 text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-500'
                      }`}
                    >
                      {label}
                    </button>
                  ))}
                </div>
              </div>
            </div>
          </section>

          {/*   */}
          <section>
            <h3 className="text-sm font-semibold text-gray-500 dark:text-gray-400 mb-3 uppercase tracking-wider">
              
            </h3>

            <div className="bg-gray-50 dark:bg-gray-700/50 rounded-2xl divide-y divide-gray-200 dark:divide-gray-600">
              {/*   */}
              <div className="flex items-center justify-between p-4">
                <div className="flex items-center gap-3">
                  <Bell size={20} className="text-green-500" />
                  <div>
                    <p className="font-medium text-gray-900 dark:text-white"> </p>
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                        
                    </p>
                  </div>
                </div>
                <button
                  onClick={handleNotificationToggle}
                  className={`w-12 h-7 rounded-full transition-colors relative ${
                    notifications ? 'bg-green-500' : 'bg-gray-300'
                  }`}
                >
                  <div
                    className={`absolute top-1 w-5 h-5 bg-white rounded-full shadow transition-transform ${
                      notifications ? 'left-6' : 'left-1'
                    }`}
                  />
                </button>
              </div>

              {/*  */}
              <div className="flex items-center justify-between p-4">
                <div className="flex items-center gap-3">
                  <Volume2 size={20} className="text-purple-500" />
                  <div>
                    <p className="font-medium text-gray-900 dark:text-white"></p>
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                       
                    </p>
                  </div>
                </div>
                <button
                  onClick={handleSoundToggle}
                  className={`w-12 h-7 rounded-full transition-colors relative ${
                    sounds ? 'bg-purple-500' : 'bg-gray-300'
                  }`}
                >
                  <div
                    className={`absolute top-1 w-5 h-5 bg-white rounded-full shadow transition-transform ${
                      sounds ? 'left-6' : 'left-1'
                    }`}
                  />
                </button>
              </div>
            </div>
          </section>

          {/*  */}
          <section>
            <h3 className="text-sm font-semibold text-gray-500 dark:text-gray-400 mb-3 uppercase tracking-wider">
              
            </h3>

            <div className="bg-gray-50 dark:bg-gray-700/50 rounded-2xl divide-y divide-gray-200 dark:divide-gray-600">
              {/*    */}
              <button
                onClick={handleResetTutorial}
                className="flex items-center justify-between p-4 w-full hover:bg-gray-100 dark:hover:bg-gray-600/50 transition-colors"
              >
                <div className="flex items-center gap-3">
                  <HelpCircle size={20} className="text-amber-500" />
                  <div className="text-left">
                    <p className="font-medium text-gray-900 dark:text-white">  </p>
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                        
                    </p>
                  </div>
                </div>
                <ChevronRight size={20} className="text-gray-400" />
              </button>

              {/*   */}
              <div className="p-4">
                <div className="flex items-center gap-3">
                  <Smartphone size={20} className="text-gray-400" />
                  <div>
                    <p className="font-medium text-gray-900 dark:text-white"> </p>
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                      v1.0.0
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>

        {/*  */}
        <div className="p-4 border-t border-gray-100 dark:border-gray-700">
          <button
            onClick={onClose}
            className="w-full py-3 bg-indigo-500 hover:bg-indigo-600 text-white rounded-xl font-semibold transition-colors"
          >
            
          </button>
        </div>
      </div>
    </div>
  );
}

export default SettingsPanel;
