// src/hooks/usePolling.js - onSnapshot  Polling Hook ( )
import { useState, useEffect, useCallback, useRef } from 'react';
import { logger } from '../utils/logger';

//  []     - Firestore  
export const POLLING_INTERVALS = {
  REALTIME: 10 * 60 * 1000,  //  10 -    (, ) (510)
  NORMAL: 30 * 60 * 1000,    //  30 -   (, ) (1530)
  LOW: 60 * 60 * 1000,       //  1 -    () (301)
  MANUAL: null               //  
};

//  []     -     
let globalInitialLoadQueue = [];
let isProcessingQueue = false;

const processInitialLoadQueue = async () => {
  if (isProcessingQueue || globalInitialLoadQueue.length === 0) return;
  isProcessingQueue = true;

  while (globalInitialLoadQueue.length > 0) {
    const fetchFn = globalInitialLoadQueue.shift();
    await fetchFn();
    //    100ms  
    await new Promise(resolve => setTimeout(resolve, 100));
  }

  isProcessingQueue = false;
};

/**
 * Firestore  (onSnapshot) Polling  Hook
 *
 * @param {Function} queryFn - Firestore   (getDocs )
 * @param {Object} options - 
 * @param {number} options.interval - Polling  (ms,  5)
 * @param {boolean} options.enabled - Polling  
 * @param {Array} options.deps -  
 *
 * @returns {Object} { data, loading, error, refetch }
 */
export const usePolling = (queryFn, options = {}) => {
  const {
    interval = POLLING_INTERVALS.NORMAL, //  []  15
    enabled = true,
    deps = []
  } = options;

  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const intervalRef = useRef(null);
  const mountedRef = useRef(true);
  const queryFnRef = useRef(queryFn);

  useEffect(() => {
    queryFnRef.current = queryFn;
  }, [queryFn]);

  const fetchData = useCallback(async () => {
    if (!enabled) {
      setLoading(false);
      return;
    }

    try {
      setError(null);
      const result = await queryFnRef.current();

      if (mountedRef.current) {
        setData(result);
        setLoading(false);
      }
    } catch (err) {
      logger.error('[usePolling]   :', err);
      if (mountedRef.current) {
        setError(err);
        setLoading(false);
      }
    }
  }, [enabled]);

  const refetch = useCallback(() => {
    setLoading(true);
    return fetchData();
  }, [fetchData]);

  useEffect(() => {
    mountedRef.current = true;

    //  interval 
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }

    // enabled false    
    if (!enabled || interval === POLLING_INTERVALS.MANUAL) {
      setLoading(false);
      return;
    }

    //  []      
    globalInitialLoadQueue.push(fetchData);
    processInitialLoadQueue();

    // Polling  (interval null  )
    if (interval && interval > 0) {
      intervalRef.current = setInterval(fetchData, interval);
    }

    return () => {
      mountedRef.current = false;
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fetchData, interval, enabled, ...deps]); // deps      ,   

  return { data, loading, error, refetch };
};

/**
 *    Polling Hook
 *
 * @param {Array<{key: string, queryFn: Function}>} queries -  
 * @param {Object} options - 
 *
 * @returns {Object} { data: {key: data}, loading, errors, refetchAll }
 */
export const useMultiPolling = (queries, options = {}) => {
  const {
    interval = POLLING_INTERVALS.NORMAL, //  []  15
    enabled = true,
    deps = []
  } = options;

  const [data, setData] = useState({});
  const [loading, setLoading] = useState(true);
  const [errors, setErrors] = useState({});
  const intervalRef = useRef(null);
  const mountedRef = useRef(true);

  const fetchAllData = useCallback(async () => {
    if (!enabled || queries.length === 0) {
      setLoading(false);
      return;
    }

    try {
      const results = await Promise.allSettled(
        queries.map(({ key, queryFn }) =>
          queryFn().then(result => ({ key, result }))
        )
      );

      if (mountedRef.current) {
        const newData = {};
        const newErrors = {};

        results.forEach((result, index) => {
          const { key } = queries[index];

          if (result.status === 'fulfilled') {
            newData[key] = result.value.result;
          } else {
            newErrors[key] = result.reason;
            logger.error(`[useMultiPolling] ${key}  :`, result.reason);
          }
        });

        setData(newData);
        setErrors(newErrors);
        setLoading(false);
      }
    } catch (err) {
      logger.error('[useMultiPolling]   :', err);
      if (mountedRef.current) {
        setLoading(false);
      }
    }
  }, [queries, enabled]);

  const refetchAll = useCallback(() => {
    setLoading(true);
    return fetchAllData();
  }, [fetchAllData]);

  useEffect(() => {
    mountedRef.current = true;

    //  interval 
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }

    // enabled false      
    if (!enabled || queries.length === 0 || interval === POLLING_INTERVALS.MANUAL) {
      setLoading(false);
      return;
    }

    //    
    fetchAllData();

    // Polling  (interval null  )
    if (interval && interval > 0) {
      intervalRef.current = setInterval(fetchAllData, interval);
    }

    return () => {
      mountedRef.current = false;
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fetchAllData, interval, enabled, ...deps]); // deps      ,   

  return { data, loading, errors, refetchAll };
};

export default usePolling;
