// src/hooks/useServiceWorker.js
//       

import { useState, useEffect, useCallback } from 'react';

import { logger } from "../utils/logger";
export function useServiceWorker() {
  const [registration, setRegistration] = useState(null);
  const [updateAvailable, setUpdateAvailable] = useState(false);
  const [isOnline, setIsOnline] = useState(navigator.onLine);

  useEffect(() => {
    // /  
    const handleOnline = () => setIsOnline(true);
    const handleOffline = () => setIsOnline(false);

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    //   
    if ('serviceWorker' in navigator) {
      registerServiceWorker();
    }

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  //  [] interval ID  state (cleanup )
  const [updateIntervalId, setUpdateIntervalId] = useState(null);

  //  [] cleanup  effect
  useEffect(() => {
    return () => {
      if (updateIntervalId) {
        clearInterval(updateIntervalId);
      }
    };
  }, [updateIntervalId]);

  const registerServiceWorker = async () => {
    try {
      const reg = await navigator.serviceWorker.register('/sw.js', {
        scope: '/'
      });

      setRegistration(reg);
      logger.log('[PWA]    :', reg.scope);

      //  
      reg.addEventListener('updatefound', () => {
        const newWorker = reg.installing;
        if (newWorker) {
          newWorker.addEventListener('statechange', () => {
            if (newWorker.state === 'installed' && navigator.serviceWorker.controller) {
              //      (1 )
              const lastUpdated = localStorage.getItem('alchan_updated');
              if (lastUpdated && Date.now() - parseInt(lastUpdated) < 60 * 60 * 1000) {
                logger.log('[PWA]   -  ');
                return;
              }
              logger.log('[PWA]    ');
              setUpdateAvailable(true);
            }
          });
        }
      });

      //  []    (1) - cleanup  ID 
      const intervalId = setInterval(() => {
        reg.update();
      }, 60 * 60 * 1000);
      setUpdateIntervalId(intervalId);

    } catch (error) {
      logger.error('[PWA]    :', error);
    }
  };

  const updateServiceWorker = useCallback(() => {
    //   
    setUpdateAvailable(false);

    // localStorage    (     )
    localStorage.setItem('alchan_updated', Date.now().toString());

    if (registration && registration.waiting) {
      //      
      navigator.serviceWorker.addEventListener('controllerchange', () => {
        logger.log('[PWA]     - ');
        window.location.reload();
      });

      //      
      registration.waiting.postMessage({ type: 'SKIP_WAITING' });
    } else {
      // waiting      
      logger.log('[PWA]      -  ');
      window.location.reload();
    }
  }, [registration]);

  const requestNotificationPermission = useCallback(async () => {
    if (!('Notification' in window)) {
      logger.log('[PWA]     .');
      return false;
    }

    if (Notification.permission === 'granted') {
      return true;
    }

    if (Notification.permission !== 'denied') {
      const permission = await Notification.requestPermission();
      return permission === 'granted';
    }

    return false;
  }, []);

  const showNotification = useCallback(async (title, options = {}) => {
    const hasPermission = await requestNotificationPermission();

    if (hasPermission && registration) {
      registration.showNotification(title, {
        icon: '/icons/icon-192x192.png',
        badge: '/icons/icon-72x72.png',
        vibrate: [100, 50, 100],
        ...options
      });
    }
  }, [registration, requestNotificationPermission]);

  return {
    registration,
    updateAvailable,
    updateServiceWorker,
    isOnline,
    requestNotificationPermission,
    showNotification
  };
}

export default useServiceWorker;
